




















import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class Test1 extends Vue {
  @Prop() private msg?: string;
  private selected: string = "";
  private noSelected: boolean = false;
  private yesSelected: boolean = false;
  private yesSeletedTimeOut: boolean = false;

  private options: any = [
    { text: "Không", value: "0" },
    { text: "Có", value: "1" }
  ];

  private yesClick() {
    this.noSelected = false;
    this.yesSelected = true;
    this.yesSeletedTimeOut = false;

    setTimeout(() => {
      this.yesSelected = false;
      this.yesSeletedTimeOut = true;
      window.open('https://www.google.com/search?rlz=1C1GCEA_enVN869VN869&ei=9TfSXZnKONyGr7wPzeKnoAU&q=ch%E1%BB%AFa+b%E1%BB%87nh+h%C3%A2m&oq=ch%E1%BB%AFa+b%E1%BB%87nh+h%C3%A2m&gs_l=psy-ab.3..0i22i30l3.3814.8084..8334...11.5..5.116.2210.17j7......0....1..gws-wiz.......0i71j0i131j0j0i67j38.8VcaIbCIGuA&ved=0ahUKEwjZmJHzjvPlAhVcw4sBHU3xCVQQ4dUDCAs&uact=5','_blank');
    }, 3000);
  }

  private noClick() {
    this.$notify.error({
      title: "Error",
      message: "Đáp án không được chấp nhận!"
    });
    this.noSelected = true;
    this.yesSelected = false;
    this.yesSeletedTimeOut = false;
    this.selected = "";
  }
}
